import React from 'react';

const ContactInfo = () => (
  <>
    <h2 className="mb-4">Контакти</h2>
    <p><strong>Телефон:</strong> +380 (99) 123-45-67</p>
    <p><strong>Email:</strong> info@example.com</p>
    <p><strong>Адреса:</strong> м. Полтава, Крайній провулок</p>
    <div className="d-flex gap-3 mt-3">
      <a href="https://t.me/yourtelegram" target="_blank" rel="noopener noreferrer">
        <i className="bi bi-telegram fs-3 text-dark"></i>
      </a>
      <a href="https://instagram.com/yourinstagram" target="_blank" rel="noopener noreferrer">
        <i className="bi bi-instagram fs-3 text-dark"></i>
      </a>
    </div>
  </>
);

export default ContactInfo;
