import React from 'react';
import './About_us.css'; // Стилі окремо
import team from './img/team.jpg'

const About = () => {
  return (
    <div className="about-page container">
      <section className="about-header">
        <h1 className='h1'>Про нас</h1>
        <p className='header_text'>Ми допомагаємо абітурієнтам впевнено готуватись до НМТ 2025 року</p>
      </section>

      <section className="about-content grid">
        <div className="about-image">
          <img src={team} alt="Наша команда" />
        </div>
        <div className="about-text">
          <h2>Хто ми?</h2>
          <p>
            Ми — команда викладачів, студентів і розробників, яка створила цей портал для того, щоб зробити підготовку до НМТ зручною, цікавою та доступною.
          </p>
          <p>
            У нас ви знайдете тести, поради, лайфхаки, корисні матеріали та підтримку. 
          </p>
          <button className="btn-black">Приєднатися</button>
        </div>
      </section>

      <section className="about-highlights">
        <h2>Наші переваги</h2>
        <div className="highlight-cards">
          <div className="card">
            <h3>💡 Ефективність</h3>
            <p>Ми зібрали лише те, що реально працює.</p>
          </div>
          <div className="card">
            <h3>📚 Різноманіття</h3>
            <p>Завдання, відео, поради, матеріали — все в одному місці.</p>
          </div>
          <div className="card">
            <h3>🤝 Підтримка</h3>
            <p>Ми не просто платформа — ми спільнота.</p>
          </div>
        </div>
      </section>

      <section className="about-quote">
        <blockquote>
          "НМТ — не вирок. Підготовка — не стрес. Все можливо з правильним підходом."
        </blockquote>
        <p>— Команда проєкту</p>
      </section>
    </div>
  );
};

export default About;
