import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import logo from './img/logo.svg';  

const Header = () => {
  const [isCollapsed, setIsCollapsed] = useState(true);

  const toggleNavbar = () => {
    setIsCollapsed(!isCollapsed);
  };

  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-black">
      <div className="container">
        <Link className="navbar-brand" to="/">
          <img src={logo} alt="Логотип" width="120" />
        </Link>

        <button
          className="navbar-toggler"
          type="button"
          onClick={toggleNavbar}
          aria-controls="navbarNav"
          aria-expanded={!isCollapsed}
          aria-label="Перемикач навігації"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className={`collapse navbar-collapse ${!isCollapsed ? 'show' : ''}`} id="navbarNav">
          <ul className="navbar-nav ms-auto text-center text-lg-start">
            <li className="nav-item">
              <Link className="nav-link me-3 text-white text-decoration-none" to="/" style={{ fontSize: '20px' }}>
                Головна
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link me-3 text-white text-decoration-none" to="/about" style={{ fontSize: '20px' }}>
                Про нас
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link me-3 text-white text-decoration-none" to="/gallery" style={{ fontSize: '20px' }}>
                Галерея
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link me-3 text-white text-decoration-none" to="/news" style={{ fontSize: '20px' }}>
                Новини
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link me-3 text-white text-decoration-none" to="/contact" style={{ fontSize: '20px' }}>
                Контакти
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Header;
