                                                                                            import React from "react";
import "./AllNews.css";
import first_news from './img/1.png'
import second_news from './img/2_2.jpg'
import third_news from './img/3_3.png'
import fourth_news from './img/4.png'
import fifth_news from './img/5.jpg'
import sixth_news from './img/6.jpg'
import seventh_news from './img/7.jpg'
import eights_news from './img/8.jpg'
import ninth_news from './img/9.jpg'
import ten_news from './img/1.jpg'

const newsData = [
  {
    id: 1,
    title: "Реєстрація на НМТ 2025 стартує в лютому",
    date: "2025-01-15",
    image: first_news,
    description: "Важлива інформація щодо термінів реєстрації для всіх учасників НМТ 2025."
  },                                                  
  {
    id: 2,
    title: "МОН затвердив структуру тесту з математики",
    date: "2025-02-01",
    image: second_news,
    description: "Оновлені вимоги до тесту з математики, який є обов'язковим у 2025 році."
  },
  {
    id: 3,
    title: "З'явився демонстраційний варіант НМТ 2025",
    date: "2025-02-10",
    image: third_news,
    description: "Офіційний демонстраційний тест допоможе ознайомитися з форматом завдань та оцінити свою готовність до реального НМТ."
  },
  {
    id: 4,
    title: "Підготовчі курси стартують з березня",
    date: "2025-02-20",
    image: fourth_news,
    description: "Запрошуємо учнів 11 класів на інтенсивну підготовку до НМТ — доступні онлайн і офлайн формати занять."
  },
  {
    id: 5,
    title: "Зміни в тесті з української мови",
    date: "2025-03-01",
    image: fifth_news,
    description: "До тесту з української мови додано нові завдання на текстову грамотність. Ознайомтесь з оновленнями!"
  },
  {
    id: 6,
    title: "Графік проведення НМТ опубліковано",
    date: "2025-03-10",
    image: sixth_news,
    description: "МОН опублікувало розклад основної та додаткової сесій НМТ 2025. Дізнайтесь свою дату тестування."
  },
  {
    id: 7,
    title: "Онлайн симулятор тестів запущено",
    date: "2025-03-20",
    image: seventh_news,
    description: "Відтепер ви можете проходити тести в онлайн-режимі з автоматичною перевіркою відповідей."
  },
  {
    id: 8,
    title: "Секрети ефективної підготовки до НМТ",
    date: "2025-04-01",
    image: eights_news,
    description: "Поради від викладачів та випускників: як організувати навчання, щоб не згоріти і скласти тест на максимум."
  },
  {
    id: 9,
    title: "Вебінар для абітурієнтів – безкоштовно",
    date: "2025-04-10",
    image: ninth_news,
    description: "Долучайтеся до безкоштовного вебінару з експертами з НМТ — корисна інформація та відповіді на запитання."
  },
  {
    id: 10,
    title: "Доступні мобільні додатки для підготовки",
    date: "2025-04-20",
    image: ten_news,
    description: "Огляд найкращих мобільних застосунків для підготовки до тесту з української, математики та історії."
  },

];

function AllNews() {
  return (
    <div className="all-news container mt-4">
      <h2 className="mb-4">Усі новини</h2>
      {newsData.map((news) => (
        <div key={news.id} className="news-row mb-4">
          <img src={news.image} alt={news.title} className="news-img" />
          <div className="news-text">
            <h4>{news.title}</h4>
            <p>{news.description}</p>
            <div className="d-flex justify-content-between align-items-center">
              <small className="text-muted">{news.date}</small>
              <button className="btn btn-sm btn-black">Читати</button>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}

export default AllNews;
