import React from 'react';
import logo from './img/logo.svg';

const Footer = () => {
  return (
    <footer className="text-light py-4 mt-5" style={{ background: 'black' }}>
      <div className="container d-flex flex-column flex-md-row justify-content-between align-items-center">

        {/* Лого + назва */}
        <div className="mb-3 mb-md-0 d-flex align-items-center">
          <img src={logo} alt="Логотип" style={{ height: '40px', marginRight: '10px' }} />
          <span className="h5 mb-0">Easy NMT</span>
        </div>

        {/* Навігація */}
        <ul className="list-unstyled d-flex mb-3 mb-md-0">
          <li className="ms-3">
            <a href="/" className="text-light text-decoration-none">Головна</a>
          </li>
          <li className="ms-3">
            <a href="/about" className="text-light text-decoration-none">Про нас</a>
          </li>
          <li className="ms-3">
            <a href="/nmt2025" className="text-light text-decoration-none">Про НМТ</a>
          </li>
          <li className="ms-3">
            <a href="/contact" className="text-light text-decoration-none">Контакти</a>
          </li>
        </ul>

        {/* Соц. мережі */}
        <div className="d-flex">
          <a
            href="https://www.instagram.com/"
            target="_blank"
            rel="noopener noreferrer"
            className="text-light fs-4 ms-3"
          >
            <i className="bi bi-instagram"></i>
          </a>
          <a
            href="https://t.me/"
            target="_blank"
            rel="noopener noreferrer"
            className="text-light fs-4 ms-3"
          >
            <i className="bi bi-telegram"></i>
          </a>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
