import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

const CarouselComponent = () => {
  return (
    <div id="nmtCarousel" className="carousel slide mb-4" data-bs-ride="carousel">
      <div className="carousel-inner">

        {/* Слайд 1 */}
        <div className="carousel-item active bg-black text-white">
          <div className="d-flex justify-content-center align-items-center text-center py-5" style={{ height: '250px' }}>
            <div>
              <h3 className="h2">Підготовка до НМТ</h3>
              <p className="d-none d-md-block">Навчайся ефективно та цікаво!</p>
            </div>
          </div>
        </div>

        {/* Слайд 2 */}
        <div className="carousel-item bg-black text-white">
          <div className="d-flex justify-content-center align-items-center text-center py-5" style={{ height: '250px' }}>
            <div>
              <h3 className="h2">Онлайн-тести</h3>
              <p className="d-none d-md-block">Практичні завдання з прикладами</p>
            </div>
          </div>
        </div>

        {/* Слайд 3 */}
        <div className="carousel-item bg-black text-white">
          <div className="d-flex justify-content-center align-items-center text-center py-5" style={{ height: '250px' }}>
            <div>
              <h3 className="h2">Поради та лайфхаки</h3>
              <p className="d-none d-md-block">Готуйся легко, з нами!</p>
            </div>
          </div>
        </div>
      </div>

      {/* Кнопки навігації */}
      <button className="carousel-control-prev" type="button" data-bs-target="#nmtCarousel" data-bs-slide="prev">
        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
        <span className="visually-hidden">Попередній</span>
      </button>
      <button className="carousel-control-next" type="button" data-bs-target="#nmtCarousel" data-bs-slide="next">
        <span className="carousel-control-next-icon" aria-hidden="true"></span>
        <span className="visually-hidden">Наступний</span>
      </button>
    </div>
  );
};

export default CarouselComponent;
