import React from 'react';

const ContactForm = () => (
  <div style={{ maxWidth: '800px', margin: '0 auto' }}>
    <h3 className="mb-4 text-center">Зв’яжіться з нами</h3>
    <form>
      <div className="mb-3">
        <label htmlFor="name" className="form-label">ПІБ</label>
        <input type="text" className="form-control" id="name" placeholder="Ваше повне ім’я" />
      </div>
      <div className="mb-3">
        <label htmlFor="email" className="form-label">Електронна пошта</label>
        <input type="email" className="form-control" id="email" placeholder="name@example.com" />
      </div>
      <div className="mb-3">
        <label htmlFor="phone" className="form-label">Телефон</label>
        <input type="tel" className="form-control" id="phone" placeholder="+380..." />
      </div>
      <div className="mb-3">
        <label htmlFor="callbackDate" className="form-label">Бажана дата зворотного дзвінка</label>
        <input type="text" className="form-control" id="callbackDate" placeholder="Оберіть дату" />
      </div>
      <div className="mb-3">
        <label htmlFor="comment" className="form-label">Коментар</label>
        <textarea className="form-control" id="comment" rows="4" placeholder="Ваші думки або побажання..."></textarea>
      </div>
      <button type="submit" className="btn btn-primary w-100" style={{ backgroundColor: 'black', border: 'none' }}>
        Надіслати
      </button>
    </form>
  </div>
);

export default ContactForm;
