import React from "react";
import "./news_style.css"; 
import first_news from './img/1.png'
import second_news from './img/2_2.jpg'
import third_news from './img/3_3.png'
import fourth_news from './img/4.png'
import fifth_news from './img/5.jpg'
import sixth_news from './img/6.jpg'
import seventh_news from './img/7.jpg'
import eights_news from './img/8.jpg'
import ninth_news from './img/9.jpg'
import ten_news from './img/1.jpg'

const newsData = [
  {
    id: 1,
    title: "Реєстрація на НМТ 2025 стартує в лютому",
    date: "2025-01-15",
    image: first_news
  },
  {
    id: 2,
    title: "МОН затвердив структуру тесту з математики",
    date: "2025-02-01",
    image: second_news
  },
  {
    id: 3,
    title: "З'явився демонстраційний варіант НМТ 2025",
    date: "2025-02-10",
    image: third_news
  },
  {
    id: 4,
    title: "Підготовчі курси стартують з березня",
    date: "2025-02-20",
    image: fourth_news
  },
  {
    id: 5,
    title: "Зміни в тесті з української мови",
    date: "2025-03-01",
    image: fifth_news
  },
  {
    id: 6,
    title: "Графік проведення НМТ опубліковано",
    date: "2025-03-10",
    image: sixth_news
  },
  {
    id: 7,
    title: "Онлайн симулятор тестів запущено",
    date: "2025-03-20",
    image: seventh_news
  },
  {
    id: 8,
    title: "Секрети ефективної підготовки до НМТ",
    date: "2025-04-01",
    image: eights_news
  },
  {
    id: 9,
    title: "Вебінар для абітурієнтів – безкоштовно",
    date: "2025-04-10",
    image: ninth_news
  },
  {
    id: 10,
    title: "Доступні мобільні додатки для підготовки",
    date: "2025-04-20",
    image: ten_news
  },
];

const NewsBlock = () => {
  return (
    <section className="news-section container">
      <h2 className="text-center mb-4">Останні новини</h2>
      <div className="news-grid">
        {newsData.map((item) => (
          <div className="news-card" key={item.id}>
            <img src={item.image} alt={item.title} className="news-image" />
            <div className="news-content">
              <h3 className="news-title">{item.title}</h3>
              <p className="news-desc">{item.description}</p>
              <button className="read-btn">Читати</button>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default NewsBlock;
