import React from "react";
import "./Gallery.css";
import first_news from './img/1.png'
import second_news from './img/2_2.jpg'
import third_news from './img/3_3.png'
import fourth_news from './img/4.png'
import fifth_news from './img/5.jpg'
import sixth_news from './img/6.jpg'
import seventh_news from './img/7.jpg'
import eights_news from './img/8.jpg'
import ninth_news from './img/9.jpg'


const galleryImages = [
  first_news,
  second_news,
  third_news,
  fourth_news,
  fifth_news,
  sixth_news,
  seventh_news,
  eights_news,
  ninth_news
];

function Gallery() {
  return (
    <div className="gallery container mt-4">
      <h2 className="mb-4">Галерея</h2>
      <div className="gallery-grid">
        {galleryImages.map((src, index) => (
          <div key={index} className="gallery-item">
            <img src={src} alt={`Фото ${index + 1}`} />
          </div>
        ))}
      </div>
    </div>
  );
}

export default Gallery;
